import { Value, Block, Inline } from '@ali/4ever-cangjie';

export default function getCurrentLink(
  value: Value,
  block: Block,
): Inline | null {
  if (!value) return null;
  const { document, selection, startBlock } = value;

  if (!selection || (!block && !selection.getStart(document).key)) {
    return null;
  }

  const parent = document.getParent(startBlock!.key) as Inline;
  return parent && parent.type === 'link' ? parent : null;
}
